import {
    Form,
    Input,
    Button,
    Upload,
    Progress,
    Image
} from 'antd';
import { css } from '@emotion/react';
import {
    InboxOutlined,
} from '@ant-design/icons';
import { useState } from 'react';


const CampaignForm = ({ onFinish, isEdit, ...props }) => {
    const [form] = Form.useForm();


    const [defaultFileList, setDefaultFileList] = useState(props.initialValues ? [{
        id: -1,
        status: "done",
        url: asset("storage/" + props.initialValues.banner)
    }] : []);
    const [progress, setProgress] = useState(0);

    const uploadImage = async options => {
        const { onSuccess, onError, file, onProgress } = options;

        const fmData = new FormData();
        const config = {
            headers: { "content-type": "multipart/form-data" },
            onUploadProgress: event => {
                const percent = Math.floor((event.loaded / event.total) * 100);
                setProgress(percent);
                if (percent === 100) {
                    setTimeout(() => setProgress(0), 1000);
                }
                onProgress({ percent: (event.loaded / event.total) * 100 });
            }
        };
        fmData.append("file", file);
        try {
            const res = await axios.post(
                "/api/admin/upload",
                fmData,
                config
            );

            return onSuccess(res.data.file);
        } catch (err) {
            console.log("Eroor: ", err);
            const error = new Error("Some error");
            onError({ err });
        }
    };


    const handleOnChange = ({ file, fileList, event }) => {
        // console.log(file, fileList, event);
        //Using Hooks to update the state to the current filelist
        if(file.status=="done"){
            setDefaultFileList([{
                id:file.uid,
                status:"done",
                url:asset("storage/"+file.response)
            }]);

        }
        //filelist - [{uid: "-1",url:'Some url to image'}]
    };

    return <Form
        css={css`width:500px`}
        labelCol={{
            span: 6,
        }}
        form={form}
        onFinish={onFinish}
        scrollToFirstError
        {...props}
    >

        <Form.Item
            name="name"
            label="Tn chin dch"
            rules={[
                {
                    required: true,
                    message: 'Vui lng nhp tn chin dch',
                },
            ]}
        >
            <Input />
        </Form.Item>
        <Form.Item
            name="url"
            label="Link kho hc"
            rules={[
                {
                    required: true,
                    message: 'Vui lng nhp link chin dch',
                },
            ]}
        >
            <Input type="url"/>
        </Form.Item>
        <Form.Item
            name="slug"
            label="Slug"
        >
            <Input />
        </Form.Item>
        <Form.Item label="Banner">
            {
                defaultFileList.length > 0 &&
                <Image src={defaultFileList[0].url} />
            }
            <Form.Item
                rules={[
                    {
                        required: true,
                        message: 'Vui lng ti banner cho chin dch',
                    },
                ]}
                name="banner" valuePropName="file" getValueFromEvent={(e) => e && e.file.response} noStyle>
                <Upload.Dragger
                    name="file"
                    accept="image/*"
                    multiple={false}
                    customRequest={uploadImage}
                    onChange={handleOnChange}
                    listType="picture-card"
                    defaultFileList={defaultFileList}
                    className="image-upload-grid"
                    showUploadList={false}
                >
                    <div>
                        <p className="ant-upload-drag-icon">
                            <InboxOutlined />
                        </p>
                        <p className="ant-upload-text">Click or drag file to this area to upload</p>
                        <p className="ant-upload-hint">Support for a single or bulk upload.</p>
                    </div>
                    {progress > 0 ? <Progress percent={progress} /> : null}
                </Upload.Dragger>
            </Form.Item>
        </Form.Item>
        <Form.Item className="float-right">
            <Button type="primary" htmlType="submit">
                {isEdit ? "Cp nht" : "Thm mi"}
            </Button>
        </Form.Item>
    </Form>
}

export default CampaignForm;
